.section {
  $this: &;
  padding-top: 50px;
  padding-bottom: 50px;
  position: relative;
  z-index: 1;
  @include xl {
    padding-top: 40px;
    padding-bottom: 40px; }
  @include lg {
    padding-top: 30px;
    padding-bottom: 30px; }
  // +md
  //   padding-top: 20px
  //   padding-bottom: 20px
  // +xs
  //   padding-top: 15px
  //   padding-bottom: 15px
  &__top {
    @include flex(space-between, flex-end);
    margin-bottom: 25px;
    @include sm {
      display: block; }
    .section__title {
      margin-bottom: 10px; } }
  &__top-block {
    display: block; }
  &__breadcrumbs {
    // margin-bottom: 30px
    // +md
 }    //   margin-bottom: 15px
  &__nav-title {
    font-weight: 700;
    text-transform: uppercase;
    font-size: 10px;
    margin-bottom: 30px;
    color: $black;
    @include md {
      margin-bottom: 15px; } }
  &__title {
    margin-bottom: 45px;
    @include xl {
      margin-bottom: 35px; }
    @include lg {
      margin-bottom: 25px; }
    @include md {
      margin-bottom: 15px; } }
  &__more {
    text-align: center;
    padding-top: 20px;
    @include md {
      padding-top: 10px; } }
  &__filter {
    max-width: 820px;
    margin-bottom: 35px;
    @include md {
      margin-bottom: 15px; } } }

.section-top {
  position: relative;
  z-index: 1;
  @include flex(flex-start, center, row, wrap);
  .shadow-rect {
    margin-top: 30px;
    @include lg {
      margin-top: 15px; } }
  @include xl {
    display: block; }
  &__title {
    max-width: 60%;
    margin-right: 45px;
    @include xl {
      margin-bottom: 15px;
      max-width: none;
      margin-right: 0; }
    @include lg {
      margin-bottom: 10px; } } }
